/* Font */
@font-face {
  font-family: tawbah100;
  src: url(./assets/fonts/tawbah100.ttf);
}

@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@500;600&display=swap');

body {font-family: 'Raleway', sans-serif;background-color: #f7f7f7;}

.content {width: 100%;position: absolute;top:0;bottom: 0;left: 0;right: 0;margin: auto;}

/* slider */

.slider {background-color: rgb(255, 255, 255); border-bottom: 0.5px solid #d6d6d6; display: flex;justify-content: center;align-items: center;text-align: center;min-height: 60vh;width: 100%;}

.text-wrapper {width: 90%;top:0;bottom: 0;left: 0;right: 0; margin: auto;margin-bottom: 100px;padding: 0px 20px 0px 20px;}

.ayah-text {font-family: tawbah100;font-size: larger;}

.translation-text {font-size: medium;}

/* sabiqun cards */

.sabiqun-tab {background-color:  #f8f8f8; top:0;bottom: 0;left: 0;right: 0;margin: 0 auto; padding: 7% 5% 0% 5%;}


.sabiquns { background-color: #ffffff;max-width: 1200px;margin: 0 auto;display: grid;grid-gap: 1rem;  padding: 2% 2% 0% 2%; border-radius: 30px;}


.sabiqun-card {background-color: #ffffff;padding: 0px 10px 0px 10px;border-radius: 4px; border-bottom: 0.3px solid #eaeaea}



.sabiqun-header {display: flex;justify-content: space-between;align-items: center;}

.sabiqun-title, .heart-button {display: inline-block;vertical-align: middle;justify-content: space-between;}


.sabiqun-title {text-align: left; color: #3c3c3c; font-size: 15px; padding-bottom: 3px}
.sabiqun-card:hover .sabiqun-title {color: #000000}


.heart-button {text-align: right;}

@media (min-width: 600px) {.sabiquns { grid-template-columns: repeat(2, 1fr); }}
@media (min-width: 900px) {.sabiquns { grid-template-columns: repeat(3, 1fr); }}
@media (max-width: 1123px) {.sabiqun-title{font-size: 13px;}}